import React from 'react';
import styleCSS from './Header.module.css';
import Menu from '../../components/Menu/Menu';
// import logo from '../../images/WebLogo1.png';
import logo from '../../images/images.png';

const Header = (props) => {

    const scrollToSection = (id) => {
        const section = document.getElementById(id);
        section.scrollIntoView({behavior: 'smooth'});
    };

    return (
        <header className={styleCSS.header}>
            <div className={styleCSS.gridHeader}>
                {/*<div>*/}
                {/*    {props.data.isMobile ? (*/}
                {/*        <Menu className={styleCSS.menuSandwich}/>*/}
                {/*    ) : (null)}*/}
                {/*</div>*/}

                <div>
                    {props.data.isMobile ? (null) :
                        (
                            <nav className={styleCSS.menuLine}>
                                <div><a onClick={() => scrollToSection('home')}>
                                    <img alt="" src={logo} className={styleCSS.logo}/>
                                </a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('services')}>Services</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('services')}>How we work</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('about')}>About Us</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('contact')}>Contact Us</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('contact')}>Blog</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('contact')}>FAQs</a></div>
                                <div className={styleCSS.menuItem}><a onClick={() => scrollToSection('contact')}>Sign in</a></div>
                            </nav>
                        )}
                </div>
            </div>

        </header>
    );
}

export default Header;

{/* <img src="images/background_0.jpg" className={styleCSS.imageHeader} />

<div className={styleCSS.textHeader}>
    <h1>{state.name}</h1>
</div> */
}